
<div>
  <virtual-scroll
    ref="virtualScroll"
    :data="list"
    :item-size="48"
    key-prop="id"
    @change="(virtualList) => tableData = virtualList">
    <el-table
      :data="tableData"
      border
      row-key="id"
      height="500px"
      show-summary
      :summary-method="getSummaries"
      style="width: 100%">
      <el-table-column
        prop="id"
        label="id"
        width="150">
      </el-table-column>
      <el-table-column
        prop="count"
        label="价格"
        width="150">
      </el-table-column>
      <el-table-column
        prop="index"
        label="index"
        width="300">
      </el-table-column>
      <el-table-column
        prop="name"
        label="姓名"
        width="420">
      </el-table-column>
      <el-table-column
        prop="province"
        label="省份"
        width="320">
      </el-table-column>
      <el-table-column
        prop="city"
        label="市区"
        width="320">
      </el-table-column>
      <el-table-column
        prop="zip"
        label="邮编"
        width="320">
      </el-table-column>
    </el-table>
  </virtual-scroll>
</div>
